// cource content actions
export const GET_COURSE_CONTENT = 'GET_COURSE_CONTENT'
export const GET_COURSE_CONTENT_SUCCESS = 'GET_COURSE_CONTENT_SUCCESS'
export const GET_COURSE_CONTENT_FAILURE = 'GET_COURSE_CONTENT_FAILURE'
export const GET_COURSE_CONTENT_INITIATE = 'GET_COURSE_CONTENT_INITIATE'

export const getCourseContent = () => ({ type: GET_COURSE_CONTENT })
export const getCourseContentSuccess = courseOutline => ({
    type: GET_COURSE_CONTENT_SUCCESS,
    payload: courseOutline,
})
export const getCourseContentFailure = () => ({ type: GET_COURSE_CONTENT_FAILURE })

export function fetchCourseContent(catalogId, documentId, contentIndex) {
    return dispatch => {
        dispatch(setSpinnerOn(1));
        fetch(process.env.REACT_APP_BLUEMIX_LABSAPI_NEW + '/caas/catalogs/' + catalogId + '/documents/' + documentId + '/all', {
            method: 'GET',
        })
            .then(res => res.json())
            .then(res => {
                if (res.error) {
                    dispatch(setSpinnerOff(1));
                    throw (res.error);
                }
                dispatch(setSpinnerOff(1));
                var contentindexData = res.find(contentData => contentData.index === contentIndex);
                if (contentindexData === undefined) {
                    // set default to 01.01
                    // dispatch(setOptionalparamset(false))
                    dispatch(setSelectedIndex('01.01'));

                } else {
                    dispatch(setOptionalparamset(true))
                    dispatch(setSelectedIndex(contentIndex));

                }
                // return res.products;
            })
            .catch(error => {
                dispatch(setSpinnerOff(1));
                console.log('error', error)
            })
    }
}

