export function clearError(errorType) {
    return dispatch => {
        dispatch({ type: CLEAR_ERROR, payload: errorType })
    }
}



export function executeNotebbokCommand(hostIp, cmd, outputid) {
    return dispatch => {
        dispatch(setSkeletonOn(1));
        fetch(process.env.REACT_APP_NOTEBOOK_API, {
            method: 'POST',
            headers: {
                'x-client-id': process.env.REACT_APP_NOTEBOOK_CLIENT_ID,
                'x-client-secret': process.env.REACT_APP_NOTEBOOK_CLIENT_SECRET_KEY,
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ host: hostIp, cmd: cmd })
        })
            .then(res => res.json())
            .then(res => {
                if (res.error) {
                    dispatch(setSkeletonOff(1));
                    throw (res.error);
                }
                const resData = {
                    apiRes: res,
                    outputId: outputid
                }
                dispatch(setSkeletonOff(1));
                dispatch(getNotebookCommandSuccess(resData));
            })
            .catch(error => {
                dispatch(setSkeletonOff(1));
                console.log('error', error)
            })
    }
}

export function fetchLabData(catalogId) {
    return dispatch => {
        dispatch(setSpinnerOn(1));
        fetch(process.env.REACT_APP_BLUEMIX_LABSAPI_NEW + '/caas/catalogs/' + catalogId, {
            method: 'GET',
        })
            .then(res => res.json())
            .then(res => {
                if (res.error) {
                    dispatch(setSpinnerOff(1));
                    throw (res.error);
                }
                dispatch(setSpinnerOff(1));
                const hasProductTitle = res.info.hasOwnProperty("productTitle")
                if (hasProductTitle && res.info.productTitle !== '') {
                    dispatch(setHeaderTitle(res.info.productTitle))
                } else {
                    dispatch(setHeaderTitle(res.title))
                }

                dispatch(setAppLayout(res.info.appLayout ? res.info.appLayout : 'default'))
                dispatch(setAppTheme(res.info.appTheme ? res.info.appTheme : 'default'))
                dispatch(setFooterPath(res.info.footer))
                dispatch(setDemos(res.info.demos ? res.info.demos : []))
                dispatch(setUsefulLinks(res.info.usefulLinks ? res.info.usefulLinks : []))
            })
            .catch(error => {
                dispatch(setSpinnerOff(1));
                console.log('error', error)
            })
    }
}

export function readTutorialData(commitId, fileName, appPrefix) {
    return dispatch => {
        dispatch(setSpinnerOn(1));
        dispatch(setFilename(fileName))
        dispatch(setContentLoadingFlag(true));
        fetch(appPrefix + commitId + '/tutorials/' + fileName, {
            method: 'GET',
        })
            .then(res => res.text())
            .then(res => {
                if (res.error) {
                    dispatch(setSpinnerOff(1));
                    throw (res.error);
                } else {
                    dispatch(setInlineModuleDelimiter("H3"));
                    dispatch(setHasInlineModules(true))

                    dispatch(getCourseContentSuccess([
                        {
                            "index": "01.01",
                            "name": fileName,
                            "content": res
                        }
                    ]
                    ));
                }
            })
            .catch(error => {
                dispatch(setSpinnerOff(1));
                console.log('error', error)
            })
    }
}

export function readLabData(commitId, appPrefix) {
    return dispatch => {
        dispatch(setSpinnerOn(1));
        fetch(appPrefix + commitId + '/tutorials/tutorial.json', {
            method: 'GET',
        })
            .then(res => res.json())
            .then(res => {
                if (res.error) {
                    dispatch(setSpinnerOff(1));
                    throw (res.error);
                }
                dispatch(setSpinnerOff(1));
                dispatch(setAppLayout(res.appLayout))
                dispatch(setAppTheme(res.appTheme))
                dispatch(setFooterPath(res.footer))
                dispatch(setDemos(res.demos))
                dispatch(setUsefulLinks(res.usefulLinks))
                dispatch(setHamburgerItems(res.tutorials))
                dispatch(setHamburgerItemsFull(res.tutorials))
            })
            .catch(error => {
                dispatch(setSpinnerOff(1));
                console.log('error', error)
            })
    }
}

export function fetchCourses(catalogId) {
    return dispatch => {
        dispatch(setSpinnerOn(1));
        fetch(process.env.REACT_APP_BLUEMIX_LABSAPI_NEW + '/caas/catalogs/' + catalogId + '/documents?labsui=true', {
            method: 'GET',
        })
            .then(res => res.json())
            .then(res => {
                if (res.error) {
                    dispatch(setSpinnerOff(1));
                    throw (res.error);
                }
                dispatch(setSpinnerOff(1));
                // dispatch(setHamburgerItems(res.nav));
                dispatch(fetchCoursesFull(catalogId, res.nav))
            })
            .catch(error => {
                dispatch(setSpinnerOff(1));
                console.log('error', error)
            })
    }
}

export function fetchCoursesFull(catalogId, itemList) {
    return dispatch => {
        dispatch(setSpinnerOn(1));
        fetch(process.env.REACT_APP_BLUEMIX_LABSAPI_NEW + '/caas/catalogs/' + catalogId + '/documents', {
            method: 'GET',
        })
            .then(res => res.json())
            .then(res => {
                if (res.error) {
                    dispatch(setSpinnerOff(1));
                    throw (res.error);
                }
                dispatch(setSpinnerOff(1));
                // dispatch(setHamburgerItemsFull(res));
                let hamRes = []
                itemList.map(item => {
                    res.map(fullitem => {
                        if (fullitem.catalog.document.documentId === item.id) {
                            if (fullitem.document.type === 'userguide') {
                                hamRes.push(item)
                            }
                        }
                    })
                })
                // dispatch(setHamburgerItems(hamRes))
            })
            .catch(error => {
                dispatch(setSpinnerOff(1));
                console.log('error', error)
            })
    }
}

