import React from "react";
import { connect } from 'react-redux'
import { CopyButton } from 'carbon-components-react'

class CodeBlock extends React.Component {

  render() {
    const {
      language,
      value,
      notebookView,
      hostIp,
      noteBookCommandResults,
      terminalView,
      documentId,
      service
    } = this.props;

    if (language === 'execute') {
        return (
          <pre className="executable__code">
          
              <code className="execute language-execute" onClick={() => { this.props.sendDataToTerminal(value, 'execute' ) }}>
                {value}
              </code>
          
          </pre>
        );
    } else if (language === 'copycommand' || language === 'copy') {
      return (
        <pre className="executable__code">
         
            <code className="copycommand language-copycommand" onClick={() => {
              this.props.sendDataToTerminal(value, 'copy')
            }}>
              {value}
            </code>
        
        </pre>
      );
    } else if (language === 'clipboard') {
      return (
        <pre className='copy_to_clipboard__code'>
          <code className='copycommand language-copycommand'>
            {value}
          </code>
          <CopyButton
            className='copybutton'
            onClick={() => {
             
              navigator.clipboard.writeText(value)
            }} />
        </pre>
      );
    } else {
      return (
        <pre className="sample__code">
          <code>
            <div dangerouslySetInnerHTML={{ __html: value }} />
          </code>
        </pre>
      );
    }
  }
}



const mapStateToProps = (state) => ({
  notebookView: state.courseDetails.notebookView,
  hostIp: state.courseDetails.hostIp,
  noteBookCommandResults: state.courseDetails.noteBookCommandResults,
  terminalView: state.courseDetails.terminalView,
  documentId: state.courseDetails.documentId,
  catalogId: state.courseDetails.catalogId,
  caasToken: state.courseDetails.caasToken,
  showQuizarea: state.courseDetails.caasToken,
  selectedTerminal: state.courseDetails.selectedTerminal,
  zoomValue: state.courseDetails.zoomValue,
  service: state.courseDetails.service,
})

const mapDispatchToProps = (dispatch) => {
  return {
    sendDataToTerminal: (data, type) => {
       if (type === 'execute') {
        window.dispatchEvent(new CustomEvent("executeCommandOnTerminal", {
          detail: { value: data  }
        }));
      } else if (type === 'copy') {
        window.dispatchEvent(new CustomEvent("copyCommandOnTerminal", {
          detail: { value: data  }
        }));
      }
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CodeBlock);
