import React from 'react'
import { withRouter, Link } from "react-router-dom";
import { Education24 } from '@carbon/icons-react';
import { useHistory } from "react-router";

const AccordionHeader = ({title, description, hamburgerItems, match}) => {
  let currentTitle = 'User Guide'
  let currentDesc = `Given below are the tutorials, links, support information related to the stack that you have chosen for deployment.`

  const history = useHistory();
  const catalogPage = {}
  if (match.params.commitId && match.params.fileName) {
    hamburgerItems.map(item => {
      if(item.tutorial_name === match.params.fileName) {
        currentTitle = item.title
        currentDesc  = item.description
      }
    })
    catalogPage.pathname = `/catalog/${match.params.commitId}`
    catalogPage.state = { fromCourse: match.params.fileName }
  }
  return (
    <header className='AccordionHeader'>
      <div className='action-bar'>
        <Education24 className="filledAlt" />
      </div>
      <div className='catalog-page--link'>
        {/* bx--link - apply carbon link styling to react router link */}
        {match.params.commitId && match.params.fileName && <Link className='bx--link' target="_self" to={catalogPage}>Back to user guide</Link>}
      </div>
      <h3>{currentTitle}</h3>
      <p>{currentDesc}</p>
    </header>
  )
}

export default withRouter(AccordionHeader)
