import React from 'react'
import ReactDOM from 'react-dom'

import App from './components/App/App'

// var catalogid = document.querySelector('openlabs-guide').getAttribute('catalogId')
var metadata = document.querySelector('openlabs-guide').getAttribute('metadata')
// var commitId = document.querySelector('openlabs-guide').getAttribute('commitId')
var appLayout = document.querySelector('openlabs-guide').getAttribute('appLayout')
var appTheme = document.querySelector('openlabs-guide').getAttribute('appTheme')
var footerPath = document.querySelector('openlabs-guide').getAttribute('footerPath')
var demos = document.querySelector('openlabs-guide').getAttribute('demos')
var usefulLinks = document.querySelector('openlabs-guide').getAttribute('usefulLinks')
var tutorials = document.querySelector('openlabs-guide').getAttribute('tutorials')
var footerContent = document.querySelector('openlabs-guide').getAttribute('footerContent')
var images = document.querySelector('openlabs-guide').getAttribute('images')

ReactDOM.render(
    <App appLayout={appLayout} appTheme={appTheme} footerPath={footerPath} demos={JSON.parse(demos)} images={JSON.parse(images)}
    usefulLinks={JSON.parse(usefulLinks)} tutorials={JSON.parse(tutorials)} footerContent={footerContent} metadata={metadata}/>,
  document.querySelector('openlabs-guide')
)
