import React, { useState } from 'react'
import { useHistory } from 'react-router'
import { Link } from 'react-router-dom'

const TableOfContents = ({
  content,
  onItemSelect
}) => {
  const [selectedItem, setSelectedItem] = useState('01.01')
  const history = useHistory()

  const selectItem = (item) => {
    setSelectedItem(item.index)
    onItemSelect(item)
  }

  return (
    <div className='TableOfContents'>
      <div className='TableOfContents__title'>
        Table of contents
      </div>
      <div className='TableOfContents__nav'>
        {content.map((item) => {
          const selected = selectedItem === item.index
          const contentIndexRegex = /[0-9]{2,}\.[0-9]{2,}$/
          const oldUrl = history.location.pathname
          const headerLocation = {
            pathname: oldUrl.replace(contentIndexRegex, item.index)
          }
          return (
            <div className={`TableOfContents__nav-item ${selected ? 'TableOfContents__nav-item--selected' : ''}`}>
              <Link className="TableOfContents__nav-link" target="_self" to={headerLocation} onClick={() => selectItem(item)}>{item.name}</Link>
            </div>
          )
        })}
      </div>
    </div>
  )
}

export default TableOfContents
