import React from 'react'
import { Link, ClickableTile } from 'carbon-components-react'
import { ArrowRight16 } from '@carbon/icons-react'

class Demos extends React.Component {
  constructor (props) {
    super(props)

    this.state = {}
  }

  componentDidMount () {}

  render () {
    const demos = this.props.demos

    return (
      <div className={`Demos ${this.props.size}`}>
        <div className='Demos__info'>
          {this.props.showTitle && <div className='Demos__title'>
            Demos
          </div>}
          <div className='Demos__description'>
            Watch technical experts walk through common use cases, highlighting product features's key capabilities.
          </div>
        </div>
        <div className='Demos__tiles'>
          {demos.map(demo => (
            <div onClick={() => { window.open(demo.url, '_blank') }} >
            <ClickableTile className='Demos__tile'>
              <div className='Demos__tile-title'>
                {demo.title}
              </div>
              <div className='Demos__tile-details'>
                <p className='description'>{demo.description}</p>
                <ArrowRight16 className='icon' />
              </div>
            </ClickableTile>
            </div>
          ))}
        </div>
      </div>
    )
  }
}

export default Demos