import React from 'react'
import { Link, ClickableTile } from 'carbon-components-react'
import { Launch16 } from '@carbon/icons-react'


class UsefulLinks extends React.Component {
  constructor (props) {
    super(props)

    this.state = {}
  }

  componentDidMount () {}

  render () {
    const usefulLinks = this.props.usefulLinks

    return (
      <div className={`UsefulLinks ${this.props.size}`}>
        <div className='UsefulLinks__info'>
          {this.props.showTitle && <div className='UsefulLinks__title'>
            Useful Links
          </div>}
          <div className='UsefulLinks__description'>
            A selection of useful links and resources for further education, help and support.
          </div>
        </div>
        <div className='UsefulLinks__tiles'>
          {usefulLinks.map(link => (
            <ClickableTile className='UsefulLinks__tile' handleClick={() => { window.open(link.url, '_blank') }}>
              <div className='UsefulLinks__tile-title'>
                {link.title}
              </div>
              <div className='UsefulLinks__tile-details'>
                <p className='description'>{link.description}</p>
                <Launch16 className='icon' />
              </div>
            </ClickableTile>
          ))}
        </div>
      </div>
    )
  }
}

export default UsefulLinks