
import React, { useEffect } from 'react'
import { connect } from 'react-redux'

import {
  fetchCourseDetails,
  fetchFooterContent,
  fetchCourses,
  fetchLabData,
  readLabData,
  readTutorialData
} from '../../actions/courseActions'
import CourseContent from '../CourseContent/CourseContent'
import { SkeletonPlaceholder } from 'carbon-components-react'
import AccordionHeader from '../AccordionHeader/AccordionHeader'

const Container = ({
  match,
  dispatch,
  courseTitle,
  setTitle,
  courseDescription,
  mainPaneldisplay,
  hamburgerItems,
  headerTitle,
  courseContent,
  Contentloading,
  footerPath,
  footerContent,
  catalogId,
  commitId,
  fileName,
  appPrefix
}) => {

  useEffect(() => {

    if(match?.params?.commitId) {
     if(match?.params?.fileName) {
       if (fileName !== match.params.fileName) {
        // dispatch(readTutorialData(match.params.commitId, match.params.fileName, appPrefix))
       }
     }
    }

  }, [match])

  // track on/off of panels
  useEffect(() => {
    if (footerPath && !footerContent) {
      dispatch(fetchFooterContent(commitId, footerPath, appPrefix));
    }
  }, [footerPath])


  // handling resize
  const [dimensions, setDimensions] = React.useState({
    height: window.innerHeight,
    width: window.innerWidth
  });

  useEffect(() => {
    const debouncedHandleResize = debounce(function handleResize() {
      setDimensions({
        height: window.innerHeight,
        width: window.innerWidth
      });
    }, 500);
    window.addEventListener("resize", debouncedHandleResize);
    return _ => {
      window.removeEventListener("resize", debouncedHandleResize);
    };
  });

  // const renderCourseOutline = () => {
  //   if (loading.courseOutline || courseOutline === undefined)
  //     return <SkeletonText paragraph={true} lineCount={25} width='100%' height='120%' />
  //   return <CourseOutline />
  // }

  const renderCourseContent = () => {
    console.log (courseContent)
    if (Contentloading || courseContent === undefined || courseContent.length === 0)
      return <SkeletonPlaceholder />
    return <CourseContent />
  }


  function debounce(fn, ms) {
    let timer;
    return _ => {
      clearTimeout(timer);
      timer = setTimeout(_ => {
        timer = null;
        fn.apply(this, arguments);
      }, ms);
    };
  }

  return (<>

    <div className="app_container">
      <AccordionHeader title={hamburgerItems.title} description={hamburgerItems.description} hamburgerItems={hamburgerItems}/>
      <div id='layout'>
        <div id='main' className={(mainPaneldisplay || (dimensions.width < 800)) ? 'open' : 'closed'}>
          {renderCourseContent()}
        </div>
      </div>
    </div>

  </>
  )
}

const mapStateToProps = state => ({
  selectedIndex: state.courseDetails.selectedOutlineIndex,
  settingsMenuFlag: state.courseDetails.settingsMenuFlag,
  fetchingCourse: state.courseDetails.fetchingCourse,
  courseTitle: state.courseDetails.courseTitle,
  courseDescription: state.courseDetails.courseDescription,
  notebookView: state.courseDetails.notebookView,
  leftPaneldisplay: state.courseDetails.leftPaneldisplay,
  rightPaneldisplay: state.courseDetails.rightPaneldisplay,
  mainPaneldisplay: state.courseDetails.mainPaneldisplay,
  courseOutline: state.courseDetails.courseOutline,
  courseContent: state.courseDetails.courseContent,
  terminalView: state.courseDetails.terminalView,
  spinnerCount: state.courseDetails.spinner,
  loading: { courseOutline: state.courseDetails.loading, courseContent: state.courseDetails.loading },
  hasErrors: { ...state.courseDetails.hasErrors },
  errorMessage: { ...state.courseDetails.errorMessage},
  metadata: state.courseDetails.metadata,
  envData: state.courseDetails.envData,
  envEndpoint: state.courseDetails.envEndpoint,
  service: state.courseDetails.service,
  rightpanelLoad: state.courseDetails.rightpanelLoad,
  tabData: state.courseDetails.tabData,
  selectedTerminal: state.courseDetails.selectedTerminal,
  brandName: state.courseDetails.brandName,
  catalogId: state.courseDetails.catalogId,
  learningPath: state.courseDetails.learningPath,
  hamburgerItems: state.courseDetails.hamburgerItems,
  documentId: state.courseDetails.documentId,
  headerTitle: state.courseDetails.headerTitle,
  learningPathIndexHeader: state.courseDetails.learningPathIndexHeader,
  learningPathIndexId: state.courseDetails.learningPathIndexId,
  selectedOutlineIndex: state.courseDetails.selectedOutlineIndex,
  stateCopy: state.courseDetails.stateCopy,
  stateOrg: state.courseDetails,
  Contentloading: state.courseDetails.Contentloading,
  appLayout: state.courseDetails.appLayout,
  footerPath: state.courseDetails.footerPath,
  footerContent: state.courseDetails.footerContent,
  remainingTime: state.courseDetails.remainingTime,
  noOfExtensions: state.courseDetails.noOfExtensions,
  metadataSpinner: state.courseDetails.metadataSpinner,
  extendSpinner: state.courseDetails.extendSpinner,
  deprovSpinner: state.courseDetails.deprovSpinner,
  serviceDetails: state.courseDetails.serviceDetails,
  labComplete: state.courseDetails.labComplete,
  markCompleteSpinner: state.courseDetails.markCompleteSpinner,
  markLabFeature: state.courseDetails.markLabFeature,
  useMemoryRouter: process.env.REACT_APP_USE_MEMORY_ROUTER,
  commitId: state.courseDetails.commitId,
  fileName: state.courseDetails.fileName,
  appPrefix: state.courseDetails.appPrefix,
})

export default connect(mapStateToProps)(Container)
