import React from 'react'
import { connect } from 'react-redux'
import CatalogCourseView from './CatalogCourseView'
import { SkeletonPlaceholder } from 'carbon-components-react'

const CourseContent = ({ courseContent, selectedIndex, Contentloading }) => {
// check selectedIndex and set the arrow navigation on off depending on slected item is first or last
  return (
    <div className="CourseContent">
      {!Contentloading ? (
          <CatalogCourseView content={courseContent.find(contentData => contentData.index === '01.01')} />
      ) : (
        <SkeletonPlaceholder />
      )}
    </div>
  )
}

const mapStateToProps = state => ({
  selectedIndex: state.courseDetails.selectedOutlineIndex,
  courseOutline: state.courseDetails.courseOutline,
  courseContent: state.courseDetails.courseContent,
  terminalView: state.courseDetails.terminalView,
  Contentloading: state.courseDetails.Contentloading,
  hasErrors: state.courseDetails.hasErrors,
})

export default connect(mapStateToProps)(CourseContent)
