import React from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import { Tabs, Tab } from 'carbon-components-react'

import { fetchCourses, fetchFooterContent, fetchLabData, readLabData } from '../../actions/courseActions'

import AccordionHeader from '../AccordionHeader/AccordionHeader'
import Tutorials from './Tutorials/Tutorials'
import UsefulLinks from './UsefulLinks/UsefulLinks'
import Demos from './Demos/Demos'

class UserGuide extends React.Component {
  constructor (props) {
    super(props)

    // be wary of adding new properties to state, because of redirect logic
    this.state = {
      key: Date.now()
    }
    this.lastVisitedCourse = null
    this.eventInterval = null
    this.resized = false
    this.size = 'small'
  }

  clearLastVisitedCourse() {
    const lastVisited = JSON.parse(localStorage.getItem('last-visited'))

    if(lastVisited !== null && lastVisited.course) {
      delete lastVisited.course
      localStorage.setItem('last-visited', JSON.stringify(lastVisited))
    }
  }

  getLastVisitedCourse() {
    const lastVisited = JSON.parse(localStorage.getItem('last-visited'))

    if(lastVisited !== null && lastVisited.course) {
      return lastVisited[lastVisited.course]
    }

    return null
  }

  componentDidUpdate (prevProps) {
    if(!prevProps.footerPath && this.props.footerPath) {
      // this.props.fetchFooterContent(this.props.commitId, this.props.footerPath, this.props.appPrefix)
      // this.props.fetchFooterContentSuccess(this.props.footerContent)
    }
  }

  componentDidMount () {
    // const catalogId = this.props.match.params.catalogId
    // const commitId = this.props.commitId
    // const appPrefix = this.props.appPrefix

    // avoid fetching again when coming back from a course
    if(!this.props.footerPath) {
     // this.props.fetchLabData(catalogId)
    } 

    if(this.props.hamburgerItems.length === 0) {
      // this.props.readLabData(commitId, appPrefix)
    } 

    // if not coming back from an existing course, get last visited course and redirect; else clear last visited course
    if (this.props.location.state && this.props.location.state.fromCourse) {
      this.clearLastVisitedCourse()
    } else {
      this.lastVisitedCourse = this.getLastVisitedCourse()
    }

    // debounce events
    this.eventInterval = setInterval(() => {
      if(this.resized) {
        this.resized = false
        // trigger resize-based rerender by updating UserGuide key
        this.setState({ key: Date.now() })
      }
    }, 500)

    // dynamic resize temporarily disabled - need resize on element but only works on window
    // window.onresize = this.onResize.bind(this)
    window.onresize = null
  }

  componentWillUnmount () {
    window.onresize = null
    if(this.eventInterval) {
      clearInterval(this.eventInterval)
    }
  }

  renderScreenUI () {
    switch (this.size) {
      case 'small':
      case 'medium':
        return this.renderSmallScreenUI()
      case 'large':
        return this.renderLargeScreenUI()
      default:
        return this.renderSmallScreenUI()
    }
  }

  renderLargeScreenUI () {
    const { hamburgerItems, usefulLinks, demos, hamburgerItemsFull, commitId} = this.props
    return (
      <div className='UserGuide__content'>
        {hamburgerItems && hamburgerItemsFull && hamburgerItemsFull !== null && <Tutorials showTitle size={this.size} commitId={commitId} hamburgerItemsFull = {hamburgerItemsFull} hamburgerItems={hamburgerItems} />}
        {usefulLinks && <UsefulLinks showTitle size={this.size} usefulLinks={usefulLinks} />}
        {demos && <Demos showTitle size={this.size} demos={demos} />}
      </div>
    )
  }

  renderSmallScreenUI () {
    const { hamburgerItems, hamburgerItemsFull, usefulLinks, demos, commitId } = this.props
    return (
      <Tabs
        className='UserGuide__tabs'
        role='navigation'
        selected={0}
        tabContentClassName='UserGuide__tabs--content'
      >
        <Tab
          key={0}
          tabIndex={0}
          role='presentation'
          label='Tutorials'
        >
          {hamburgerItems && hamburgerItemsFull && hamburgerItemsFull !== null && <Tutorials size={this.size} commitId= {commitId} hamburgerItems={hamburgerItems} hamburgerItemsFull = {hamburgerItemsFull} />}
        </Tab>
        <Tab
          key={1}
          tabIndex={1}
          role='presentation'
          label='Useful Links'
        >
          {usefulLinks && <UsefulLinks size={this.size} usefulLinks={usefulLinks} />}
        </Tab>
        <Tab
          key={2}
          tabIndex={2}
          role='presentation'
          label='Support'
        >
          {demos && <Demos size={this.size} demos={demos} />}
        </Tab>
      </Tabs>
    )
  }

  onResize () {
    this.resized = true
    const bodyElement = document.querySelector('.OpenLabsGuideApp')
    if (bodyElement) {
      const { clientWidth } = bodyElement

      if(clientWidth >= 768) {
        this.size = 'large'
      } else if(clientWidth < 768 && clientWidth >= 500) {
        this.size = 'medium'
      } else if(clientWidth < 500) {
        this.size = 'small'
      }
    }
  }

  render () {
    const headerProps = {
      title: 'User Guide',
      description: `Given below are the tutorials, links, support information related to the stack that you have chosen for deployment.`
    }

    return (
      <>
        <AccordionHeader {...headerProps} />
        <div className={`UserGuide ${this.size}`} key={this.state.key}>
          {this.renderScreenUI()}
          {this.lastVisitedCourse && <Redirect to={this.lastVisitedCourse.location} />}
        </div>
      </>
    )
  }
}

const mapStateToProps = state => ({
  footerPath: state.courseDetails.footerPath,
  hamburgerItems: state.courseDetails.hamburgerItems,
  demos: state.courseDetails.demos,
  usefulLinks: state.courseDetails.usefulLinks,
  hamburgerItemsFull: state.courseDetails.hamburgerItemsFull,
  commitId: state.courseDetails.commitId,
  appPrefix: state.courseDetails.appPrefix,
})

export default connect(mapStateToProps, { fetchCourses, fetchFooterContent, fetchLabData, readLabData})(UserGuide)
