import React from 'react';
import { Route, Switch, BrowserRouter, MemoryRouter } from 'react-router-dom';
import { Loading } from 'carbon-components-react';
import Container from './Container/Container'
import UserGuide from './UserGuide/UserGuide'
import NotFound from '../components/NotFound/NotFound'

export default function RouterComponent(props) {

  const loaderProps = {
    active: true,
    withOverlay: true,
    small: false,
    description: 'Loader',
    className: 'loader'
  };

  const { setTitle, initialRoutes } = props
  const useMemoryRouter = true
  const routes = <Switch>
    <Route exact path='/catalog/:catalogId' render={(props) => {
      return (<UserGuide {...props} />)
    }}
    />
    <Route exact path='/clone/:commitId/tutorial/:fileName' render={(props) => {
      return (<Container {...props} setTitle={setTitle} />)
    }}
    />
    <Route exact path='/:brandid/course/:docid/:contentIndex' render={(props) => {
      return (<Container {...props} setTitle={setTitle} />)
    }}
    />
    <Route exact path='/:brandid/course/:docid' render={(props) => {
      return (<Container {...props} setTitle={setTitle} />)
    }}
    />
    <Route exact path='/catalog/:catalogId/course/:docid' render={(props) => {
      return (<Container {...props} setTitle={setTitle} />)
    }}
    />
    <Route exact path='/catalog/:catalogId/course/:docid/:contentIndex' render={(props) => {
      return (<Container {...props} setTitle={setTitle} />)
    }}
    />
    <Route render={routeProps => <NotFound {...routeProps} />} />
  </Switch>

  return (
    <React.Suspense fallback={<Loading {...loaderProps} />}>
      {useMemoryRouter && <MemoryRouter initialEntries={initialRoutes} initialIndex={0}>{routes}</MemoryRouter>}
      {!useMemoryRouter && <BrowserRouter basename={process.env.REACT_APP_BASENAME}>{routes}</BrowserRouter>}
    </React.Suspense>
  )
}