
import React, { PureComponent } from "react";
import { connect } from 'react-redux'
import { setSelectedIndex } from "../../../actions/courseActions";

class imgBlock extends PureComponent {

  render() {
    let imgAlt = this.props.alt
    let imgSrc = ''
    this.props.images.filter(function (image) {
      console.log("name of image", image.name)
      console.log("image alt", imgAlt)
        if (image.name === imgAlt) {
          imgSrc = image.selectedImgPreview
        }
    });

    const { commitId, appPrefix } = this.props;
    let imageUrl = this.props.appPrefix + this.props.commitId + '/' + this.props.src
    // let imageUrl = 'http://localhost:3011' + '/' + this.props.commitId + '/' + this.props.src
      return (
        <img src={imgSrc} alt={imgAlt}></img>
      );
    }
  }

const mapStateToProps = state => ({
  commitId: state.courseDetails.commitId,
  appPrefix: state.courseDetails.appPrefix,
  images: state.courseDetails.images
})


export default connect(mapStateToProps)(imgBlock);