import React, { useEffect, useState } from 'react'
import ReactDOM from 'react-dom';
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom';
import {
    Accordion,
    AccordionItem,
} from 'carbon-components-react';
import ReactPlayer from 'react-player'
import ReactMarkdown from 'react-markdown/with-html';

import CodeBlock from './shared/code'
import imgBlock from './shared/image'
import TableOfContents from './TableOfContents/TableOfContents'

import 'github-markdown-css'

// can't put these in state; inaccessible in first useEffect otherwise
let courseDidScroll = false, contentDidScroll = false, courseResized = false

const CatalogCourseView = ({
    courseOutline,
    courseContent,
    content,
    attachmentUrl,
    metadata,
    history,
    match,
    hasInlineModules,
    inlineModuleDelimiter
}) => {
    const [courseContentProcessed, setCourseContentProcessed] = useState([]);
    const [key, setKey] = useState(Date.now())

    // keep at top; meant as substitute for componentDidMount
    useEffect(() => {
       const bodyElement = document.querySelector('.OpenLabsGuideApp')
       let updateInterval = ''
       if (bodyElement && bodyElement !== null &&  bodyElement !== undefined) {
        // debounce scroll events
       updateInterval = setInterval(() => { // need to handle clearing this interval
            if(courseDidScroll) {
                courseDidScroll = false
                const course = bodyElement
                const coursePosition = {top: course.scrollTop, left: course.scrollLeft}
                saveLastVisitedPositions({coursePosition})
            }
            if(contentDidScroll) {
                contentDidScroll = false
                const content = bodyElement.clientWidth >= 1056
                    ?  document.querySelector('.CatalogCourseView__content .Content__content')
                    : document.querySelector('.bx--accordion__item.bx--accordion__item--active .Content__content')
                const contentPosition = {top: content.scrollTop, left: content.scrollLeft}
                saveLastVisitedPositions({contentPosition})
            }
            if(courseResized) {
                courseResized = false
                // force rerender
                setKey(Date.now())
            }
            
        }, 500)

        // for course scroll
        bodyElement.onscroll = onCourseScroll
        bodyElement.onresize = onCourseResize
        }
        // clear all handlers here
        return () => {
            document.querySelector('.OpenLabsGuideApp').onScroll = null
            if(updateInterval !== '') {
                clearInterval(updateInterval)
            }
        }
    }, []) // keep this array empty; should only execute once

    useEffect(() => {
        const clickableLinks = Array.from(document.querySelectorAll('.Content__content a'))
        clickableLinks.forEach(link => {
            let videoLink = ReactPlayer.canPlay(link.href);
            if (videoLink) {
                ReactDOM.render(<ReactPlayer url={link.href} width="100%" playing={false} />, link);
            } else if (link.download && link.download === 'download') { // if the link is download type
                let linkText = link.href
                var replaceLinkText = new RegExp(window.location.origin, 'g')
                linkText = linkText.replace(replaceLinkText, attachmentUrl)
                link.href = linkText
            }
        })
    });

    useEffect(() => {
        console.log ('Use effect')
        if(hasInlineModules && courseContent.length > 0) {
            let courseContentConstructed = null
            if(inlineModuleDelimiter === process.env.REACT_APP_INLINE_MODULE_DELIMITER_HEADING) {
                courseContentConstructed =  buildModulesWithHeaderDelimiter(courseContent);
            } else {
                courseContentConstructed =  buildModulesWithBoldDelimiter(courseContent);
            }
            setCourseContentProcessed(courseContentConstructed);
            console.log ('Course Constructed')
            console.log (courseContentConstructed)
        } else {
            console.log ('Course Content')
            console.log (courseContent)
            setCourseContentProcessed(courseContent);
        }
        saveLastVisitedCourse()
        saveLastVisitedCourseState()
        // when course content is generated and rendered, navigate the user to his last known position
        if (courseContentProcessed.length !== 0) {
            scrollToSavedCoursePosition()
        }
        
    }, [courseContentProcessed.length, inlineModuleDelimiter]);


    let markdown = content.content

    // replace all variables from metadata.
    for (let key in metadata.meta) {
        if (typeof metadata.meta[key] === "object") {
            var subObject = metadata.meta[key]
            Object.keys(subObject).forEach(subKey => {
                var replaceText = new RegExp(`##${key}.${subKey}##`, 'g')
                markdown = markdown.replace(replaceText, `${subObject[subKey]}`)
            })
        }
    }

    // check for content with /attachment and replae with attachment
    var replaceAttachmenttext = new RegExp('a href=_attachments', 'g')
    markdown = markdown.replace(replaceAttachmenttext, `a href=/_attachments`)

    const bringSectionToTop = (id) => {
        const content = document.querySelector('.CatalogCourseView__content .Content__content')
        const element = document.getElementById(id)
        const bodyElement = document.querySelector('.OpenLabsGuideApp')
        courseDidScroll = true
        contentDidScroll = true
        content.scrollTo(0, element.offsetTop)
        bodyElement.scrollTo(0, 0)
    }

    const updateContentIndex = (newIndex) => {
        const contentIndexRegex = /[0-9]{2,}\.[0-9]{2,}$/
        const oldUrl = history.location.pathname
        history.replace(oldUrl.replace(contentIndexRegex, newIndex))
    }

    const scrollToSavedCoursePosition = () => {
        const bodyElement = document.querySelector('.OpenLabsGuideApp')
        const course = bodyElement
        const content = bodyElement.clientWidth >= 1056
        ?  document.querySelector('.CatalogCourseView__content .Content__content')
        : document.querySelector('.bx--accordion__item.bx--accordion__item--active .Content__content')

        const lastVisited = JSON.parse(localStorage.getItem('last-visited'))
        if(lastVisited !== null && lastVisited[match.params.docid]) {
            const { coursePosition, contentPosition } = lastVisited[match.params.docid]
            if(course && coursePosition) {
                course.scrollTo(coursePosition.left, coursePosition.top)
            }
            if(content && contentPosition) {
                content.scrollTo(contentPosition.left, contentPosition.top)
            }
        }
    }

    // having redundant implementations of the below methods that use localStorage; these are all called
    // at separate times by separate handlers
    const resetContentPosition = () => {
        saveLastVisitedPositions({contentPosition: {top: 0, left: 0}})
    }

    const saveLastVisitedCourseState = () => {
        let lastVisited = JSON.parse(localStorage.getItem('last-visited'))
        if(lastVisited === null) {
            lastVisited = { [match.params.docid]: {} }
        } else {
            if(!lastVisited[match.params.docid]) {
                lastVisited[match.params.docid] = {}
            }
        }

        lastVisited[match.params.docid].location = history.location

       // localStorage.setItem('last-visited', JSON.stringify(lastVisited))
    }

    const saveLastVisitedPositions = ({coursePosition, contentPosition}) => {
        let lastVisited = JSON.parse(localStorage.getItem('last-visited'))
        if(lastVisited === null) {
            lastVisited = { [match.params.docid]: {} }
        } else {
            if(!lastVisited[match.params.docid]) {
                lastVisited[match.params.docid] = {}
            }
        }
        if (coursePosition) lastVisited[match.params.docid].coursePosition = coursePosition
        if (contentPosition) lastVisited[match.params.docid].contentPosition = contentPosition

        // localStorage.setItem('last-visited', JSON.stringify(lastVisited))
    }

    // set course so that we know the user was last inside a course
    const saveLastVisitedCourse = () => {
        let lastVisited = JSON.parse(localStorage.getItem('last-visited'))
        if(lastVisited === null) {
            lastVisited = { course: match.params.docid }
        } else {
            lastVisited.course = match.params.docid
        }

        // localStorage.setItem('last-visited', JSON.stringify(lastVisited))
    }

    const hasLastVisited = () => {
        const lastVisited = JSON.parse(localStorage.getItem('last-visited'))
        if (lastVisited !== null && lastVisited[match.params.docid]) {
            return true
        }
        return false
    }

    const onCourseScroll = () => {
        courseDidScroll = true
    }

    const onCourseResize = () => {
        courseResized = true
    }

    const onContentScroll = () => {
        contentDidScroll = true
    }

    const renderLargeScreenUI = () => {
        return (
            <>
                <div className='CatalogCourseView__table-of-contents'>
                    <TableOfContents
                        content={courseContentProcessed}
                        onItemSelect={(section) => {
                            bringSectionToTop(`header${section.index}`)
                            updateContentIndex(section.index)
                        }}
                    />
                </div>
                <div className='CatalogCourseView__content'>
                    <div className='content'>
                        <div className="Content__content" onScroll={onContentScroll}>                            
                            {courseContentProcessed.map((courseObject, index) => (
                                courseObject.index.indexOf('.') !== -1 ? (
                                    <div className="Content__section">
                                        <h3 className="Content__section-header" id={`header${courseObject.index}`}
                                            onClick={() => {
                                                bringSectionToTop(`header${courseObject.index}`)
                                                updateContentIndex(courseObject.index)
                                            }}
                                        >{courseObject.name}</h3>
                                        <div className="markdown-body">
                                            <ReactMarkdown
                                                source={processRawMarkdown(courseObject.content, metadata)}
                                                escapeHtml={false}
                                                renderers={{ code: CodeBlock, image: imgBlock }}
                                            />
                                        </div>
                                    </div>
                                ) : (null)
                            ))}
                        </div>
                    </div>
                </div>
            </>

        )
    }

    const renderSmallScreenUI = () => {
        console.log (courseContentProcessed)
        return (
            <Accordion align="start">
            { courseContentProcessed.map(courseObject => (
                courseObject.index.indexOf('.') !== -1 ? (
                <AccordionItem
                    onClick={() => {
                        // TODO: both these methods are executed twice. Update to only do once
                        // (i.e. when user is opening the accordion item, not when closing)
                        updateContentIndex(courseObject.index)
                        saveLastVisitedCourseState()
                        resetContentPosition()
                    }}
                    onHeadingClick={function noRefCheck(){}}
                    title={courseObject.name}
                    open={hasLastVisited() && match.params.contentIndex === courseObject.index}
                >
                    {/* { outLineItems[courseObject].slice(1).map(courceItem => ( markdown += courceItem.name))} */}
                    <div className='content'>
                        {/* <ContentHeader /> */}
                        <div className="Content__content" onScroll={onContentScroll}>
    
                            <div className="markdown-body">
                                <ReactMarkdown
                                    source={processRawMarkdown(courseObject.content, metadata)}
                                    escapeHtml={false}
                                    renderers={{ code: CodeBlock, image: imgBlock }}
                                />
                            </div>
                        </div>
                    </div>
                </AccordionItem>
                ) : ( null )
            ))}
            </Accordion>
        )
    }

    const renderScreenUI = () => {
        const { clientWidth, clientHeight } = document.querySelector('.OpenLabsGuideApp')
        console.log (clientWidth)
        if (clientWidth >= 1056) {
            return renderLargeScreenUI()
        } else {
            return renderSmallScreenUI()
        }
    }    

    return (
        <div className='CatalogCourseView'>
            {renderScreenUI()}
        </div>
    )
}

const mapStateToProps = state => ({
    attachmentUrl: state.courseDetails.attachmentUrl,
    contentbyIndex: state.courseDetails.contentbyIndex,
    catalogId: state.courseDetails.catalogId,
    optionalParamset: state.courseDetails.optionalParamset,
    leftPaneldisplay: state.courseDetails.leftPaneldisplay,
    rightPaneldisplay: state.courseDetails.rightPaneldisplay,
    selectedIndex: state.courseDetails.selectedOutlineIndex,
    courseOutline: state.courseDetails.courseOutline,
    courseContent: state.courseDetails.courseContent,
    terminalView: state.courseDetails.terminalView,
    loading: { courseOutline: state.courseDetails.loading, courseContent: state.courseDetails.loading },
    hasErrors: state.courseDetails.hasErrors,
    metadata: state.courseDetails.metadata,
    caasToken: state.courseDetails.caasToken,
    hasInlineModules: state.courseDetails.hasInlineModules,
    inlineModuleDelimiter: state.courseDetails.inlineModuleDelimiter
})

function getDataFromPrent(index , dataObject, dataRequired) {
    // console.log(index, dataObject);
    if (index <= 9) {
        index = "0"+index // appending 0 as we have to group by on index which comes as string and 01 is considered lower tha 10
    }
    let data = dataObject.find(outline => outline.index === index)
    if (dataRequired === 'index') {
         return data.index
    } else {
         return data.name
    }
}

function processRawMarkdown(markdown, metadata) {
    let markdownOutput = markdown;
    // replace all variables from metadata.
    for (let key in metadata.meta) {
        if (typeof metadata.meta[key] === "object") {
            var subObject = metadata.meta[key]
            Object.keys(subObject).forEach(subKey => {
                var replaceText = new RegExp(`##${key}.${subKey}##`, 'g')
                markdownOutput = markdownOutput.replace(replaceText, `${subObject[subKey]}`)
            })
        }
    }

    // check for content with /attachment and replae with attachment
    var replaceAttachmenttext = new RegExp('a href=_attachments', 'g')
    markdownOutput = markdownOutput.replace(replaceAttachmenttext, `a href=/_attachments`);
    // console.log(markdownOutput);
    return markdownOutput;
}

function buildModulesWithBoldDelimiter(content) {
    let courseContentConstructed = [];
    //loop sections and modules
    content.forEach(item => {
        // test to identify modules from sections 
        if(item.index.indexOf('.') !== -1) {
            const [ currentSection, currentModule ] = item.index.split('.')
            let currentModuleNumber = Number(currentModule)
            //break large module into array of lines
            const contentArr = item.content.split(/\r?\n/);
            let moduleItem = {}
            // used to identify multiline title. eg.
            // **this is a large title split into 
            //  2 lines**
            let incompleteModuleTitleFlag = false
            contentArr.forEach(lineItem => {
                lineItem = lineItem.trimEnd()
                
                if(/^\*\*.*/.test(lineItem)) {
                    // identify module titles
                    if(Object.keys(moduleItem).length) {
                        courseContentConstructed.push(Object.assign({}, moduleItem));
                    }
                    if(lineItem.slice(-2) !== '**' ) {
                        moduleItem.name = lineItem.slice(2)
                        incompleteModuleTitleFlag = true
                    } else {
                        moduleItem.name = lineItem.slice(2).substring(0, lineItem.length - 4)
                        // incompleteModuleTitleFlag = false
                    }
                    moduleItem.content = '';
                    moduleItem.index = currentSection + '.' + ('0' + currentModuleNumber).slice(-2)
                    currentModuleNumber = currentModuleNumber + 1;
                } else if(/\*\*$/.test(lineItem) && incompleteModuleTitleFlag) {
                    // identify end of a multiline module title
                    moduleItem.name += ' ' + lineItem.substring(0, lineItem.length - 2)
                    incompleteModuleTitleFlag = false
                } else if( moduleItem.name ) {
                    // aggregate module content
                    moduleItem.content += lineItem + '\n';
                }
            })
            //push the last module 
            courseContentConstructed.push(Object.assign({}, moduleItem));
        }
    })
    return courseContentConstructed;
}

function buildModulesWithHeaderDelimiter(content) {
    let courseContentConstructed = [];
    console.log (content)
    //loop sections and modules
    content.forEach(item => {
        console.log (item)
        // test to identify modules from sections 
        if(item.index.indexOf('.') !== -1) {
            const [ currentSection, currentModule ] = item.index.split('.')
            let currentModuleNumber = Number(currentModule)
            //break large module into array of lines
            const contentArr = item.content.split(/\r?\n/);
            let moduleItem = {}
            contentArr.forEach(lineItem => {
                lineItem = lineItem.trimEnd()
                
                if(/^\#\#\#.*/.test(lineItem)) {
                    // identify module titles
                    if(Object.keys(moduleItem).length) {
                        courseContentConstructed.push(Object.assign({}, moduleItem));
                    }
                    moduleItem.name = lineItem.slice(3);
                    moduleItem.content = '';
                    moduleItem.index = currentSection + '.' + ('0' + currentModuleNumber).slice(-2)
                    currentModuleNumber = currentModuleNumber + 1;
                } else if( moduleItem.name ) {
                    // aggregate module content
                    moduleItem.content += lineItem + '\n';
                }
            })
            //push the last module 
            courseContentConstructed.push(Object.assign({}, moduleItem));
        }
    })
    return courseContentConstructed;
}


export default withRouter(connect(mapStateToProps)(CatalogCourseView))
