import * as actions from '../actions/courseActions'
import Cookies from 'js-cookie';

export const initialState = {
    loading: true,
    hasErrors: { GET_COURSE_CONTENT: false, GET_COURSE_OUTLINE: false, EXTEND_TIMER_DURATION: false, DEPROVISION_INSTANCE: false },
    errorMessage: { GET_COURSE_CONTENT: '', GET_COURSE_OUTLINE: '', EXTEND_TIMER_DURATION: '', DEPROVISION_INSTANCE: '' },
    courseContent: {},
    courseOutline: {},
    terminalView: false,
    selectedOutlineIndex: null,
    leftPaneldisplay: true,
    rightPaneldisplay: true,
    notebookView: false,
    optionalParamset: false,
    contentbyIndex: '',
    courseTitle: '',
    courseDescription: '',
    settingsMenuFlag: false,
    attachmentUrl: '',
    documentId: '',
    catalogId: '',
    metadata: null,
    envEndpoint: '',
    envData: '',
    hostIp: '',
    noteBookCommandResults: {},
    noteBookCommandStatus: {},
    spinner: 0,
    skeletonCount: 0,
    mainPaneldisplay: true,
    rightpanelLoad: false,
    tabData: {},
    customTabsData: {},
    customTabsDisplay: false,
    caasToken: '',
    selectedTerminal: 0,
    isSideNavExpanded: false,
    hamburgerItems: [],
    images: [],
    brandName: '',
    headerTitle: '',
    learningPath: false,
    learningPathIndexHeader: '',
    learningPathIndexId: '',
    leftNavDisplay: false,
    rightNavDisplay: true,
    learningProgress: [],
    lastVisitedSection: '',
    userProgress: '',
    quizData: {},
    badgeId: '',
    clearQuiz: false,
    showQuizarea: false,
    quizResults: {},
    quizShowResults: false,
    traverseDirection: 'next',
    stateCopy: {},
    zoomValue: 100,
    appLayout: 'default',
    footerPath: '',
    Contentloading: true,
    service: '',
    footerContent: '',
    remainingTime: 0,
    noOfExtensions: 0,
    quizSpinner: 0,
    metadataSpinner: 0,
    extendSpinner: 0,
    deprovSpinner: 0,
    serviceDetails: {},
    labComplete: false,
    markCompleteSpinner: 0,
    markLabFeature: false,
    newTabFeature: false,
    demos: [],
    usefulLinks: [],
    hamburgerItemsFull: null,
    commitId: null,
    fileName: null,
    appPrefix: ''
}

export default function courseReducer(state = initialState, action) {
    switch (action.type) {
        case actions.GET_COURSE_CONTENT_INITIATE:
            return { ...state, fetchingCourse: true, rightPaneldisplay: false, lastVisitedSection: '' }
        case actions.GET_COURSE_CONTENT:
            return { ...state, Contentloading: true }
        case actions.GET_COURSE_CONTENT_SUCCESS:
            return { ...state, courseContent: action.payload, Contentloading: false, fetchingCourse: false, hasErrors: { ...state.hasErrors, GET_COURSE_CONTENT: false } }
        case actions.GET_COURSE_CONTENT_FAILURE:
            return { ...state, loading: false, Contentloading: false, fetchingCourse: false, hasErrors: { ...state.hasErrors, GET_COURSE_CONTENT: true } }
        case actions.GET_COURSE_OUTLINE:
            return { ...state, loading: true }
        case actions.GET_COURSE_OUTLINE_SUCCESS:
            return { ...state, courseOutline: action.payload, loading: false, hasErrors: { ...state.hasErrors, GET_COURSE_OUTLINE: false } }
        case actions.GET_COURSE_OUTLINE_FAILURE:
            return { ...state, loading: false, hasErrors: { ...state.hasErrors, GET_COURSE_OUTLINE: true } }

        case actions.SET_COURSE_OUTLINE_SELECTED_INDEX:
            return { ...state, selectedOutlineIndex: action.payload, showQuizarea: false, quizShowResults: false }


        case actions.SET_MAIN_PANEL_DISPLAY:
            return { ...state, mainPaneldisplay: action.payload }

        case actions.SET_NOTEBOOK_VIEW_DISPLAY:
            return { ...state, notebookView: action.payload }
        case actions.SET_OPTIONAL_PARAM_SET:
            return { ...state, optionalParamset: action.payload }
        case actions.GET_COURSE_CONTENT_BY_INDEX_SUCCESS:
            return { ...state, contentbyIndex: action.payload }
        case actions.SET_COURSE_OUTLINE_TITLE:
            return { ...state, courseTitle: action.payload }
        case actions.SET_COURSE_OUTLINE_DESCRIPTION:
            return { ...state, courseDescription: action.payload }
        case actions.SET_SETTINGS_MENU_DISPLAY:
            return { ...state, settingsMenuFlag: action.payload }
        case actions.SET_ATTACHMENT_BASE_URL:
            return { ...state, attachmentUrl: action.payload }
        case actions.SET_CATALOG_DOCUMENT_DATA:
            return { ...state, documentId: action.payload.documentId, catalogId: action.payload.catalogId }
        case actions.GET_EXECUTE_NOTEBBOK_COMMAND_SUCCESS:
            let executeResponse = action.payload.apiRes
            let outputId = action.payload.outputId
            if (executeResponse.message === 'error') {
                state.noteBookCommandResults[outputId] = executeResponse.stderr
                state.noteBookCommandStatus[outputId] = 'error'
            } else if (executeResponse.message === 'success') {
                state.noteBookCommandResults[outputId] = executeResponse.stdout
                state.noteBookCommandStatus[outputId] = 'success'
            } else {
                state.noteBookCommandResults[outputId] = 'There was an error processing your request.'
                state.noteBookCommandStatus[outputId] = 'error'
            }
            return { ...state, noteBookCommandResults: state.noteBookCommandResults, noteBookCommandStatus: state.noteBookCommandStatus }
        case actions.SET_EXECUTE_NOTEBBOK_COMMAND_BLOCK:
            let commandBlockId = action.payload
            state.noteBookCommandResults[commandBlockId] = ''
            state.noteBookCommandStatus[commandBlockId] = ''
            return { ...state, noteBookCommandResults: state.noteBookCommandResults, noteBookCommandStatus: state.noteBookCommandStatus }
        case actions.SET_SPINNER_ON:
            return { ...state, spinner: state.spinner + 1 }
        case actions.SET_SPINNER_OFF:
            return { ...state, spinner: state.spinner - 1 }
        case actions.SET_SKELETON_ON:
            return { ...state, skeletonCount: state.skeletonCount + 1 }
        case actions.SET_SKELETON_OFF:
            return { ...state, skeletonCount: state.skeletonCount - 1 }


        case actions.SET_HAMBURGER_ITEMS_FULL:
            return { ...state, hamburgerItemsFull: action.payload }
        case actions.SET_HAMBURGER_ITEMS:
            return { ...state, hamburgerItems: action.payload }
        case actions.SET_IMAGES:
            return { ...state, images: action.payload }
        case actions.SET_BRAND_NAME:
            return { ...state, brandName: action.payload }
        case actions.SET_HEADER_TITLE:
            return { ...state, headerTitle: action.payload }

        case actions.SET_APP_LAYOUT:
            return { ...state, appLayout: action.payload }
        case actions.SET_APP_THEME:
            return { ...state, appTheme: action.payload }
        case actions.SET_HAS_INLINE_MODULES:
            return { ...state, hasInlineModules: action.payload }
        case actions.SET_INLINE_MODULE_DELIMITER:
            return { ...state, inlineModuleDelimiter: action.payload }
        case actions.SET_FOOTER_PATH:
            return { ...state, footerPath: action.payload }
        case actions.SET_USEFUL_LINKS:
            return { ...state, usefulLinks: action.payload }
        case actions.SET_DEMOS:
            return { ...state, demos: action.payload }
        case actions.FETCH_FOOTER_CONTENT_SUCCESS:
            return { ...state, footerContent: action.payload }
        case actions.SET_CONTENT_LOADING_FLAG:
            return { ...state, Contentloading: true }
        case actions.SET_METADATA:
            return { ...state, metadata: action.payload }
        case actions.SET_COMMIT_ID:
            return { ...state, commitId: action.payload }
        case actions.SET_FILE_NAME:
            return { ...state, fileName: action.payload }
        case actions.SET_PREFIX:
            return { ...state, appPrefix: action.payload }
        default:
            return state
    }
}
