import React from 'react'
import GlobalErrorHandler from './GlobalErrorHandler'
import { Provider } from 'react-redux'
import { createStore, applyMiddleware } from 'redux'
import thunk from 'redux-thunk'
import rootReducer from '../../reducers'
import '../../index.scss'
import AppContainer from '../AppContainer/AppContainer'
import {setmetaData, setCommitId, setPrefix, setDemos, setFooterPath, setHamburgerItems, setHamburgerItemsFull, setUsefulLinks, setAppLayout, setAppTheme, fetchFooterContentSuccess, setImages} from '../../actions/courseActions'

const logger = store => next => action => {
  console.group(action.type)
  console.info('dispatching', action)
  let result = next(action)
  console.log('next state', store.getState())
  console.groupEnd(action.type)
  return result
}

let createStoreWithMiddleware = ''
if (process.env.REACT_APP_ENV === 'dev') {
  createStoreWithMiddleware = applyMiddleware(logger, thunk)(createStore)
} else {
  createStoreWithMiddleware = applyMiddleware(thunk)(createStore)
}

const store = createStoreWithMiddleware(rootReducer)


export class App extends React.Component {

  render() {
    // store.dispatch(setmetaData(JSON.parse(this.props.metadata)))
    // store.dispatch(setCommitId(this.props.commitId))
    // store.dispatch(setPrefix(this.props.appenPrefix))
    console.log (this.props.appLayout)
    console.log (this.props.appTheme)
    console.log (this.props.footerPath)
    console.log (this.props.demos)
    console.log (this.props.usefulLinks)
    console.log (this.props.tutorials)
    console.log (this.props.footerContent)
    console.log (this.props.metadata)

    store.dispatch(setmetaData(JSON.parse(this.props.metadata)))
    store.dispatch(setAppLayout(this.props.appLayout))
    store.dispatch(setAppTheme(this.props.appTheme))
    store.dispatch(setFooterPath(this.props.footerPath))
    store.dispatch(setDemos(this.props.demos))
    store.dispatch(setUsefulLinks(this.props.usefulLinks))
    store.dispatch(setHamburgerItems(this.props.tutorials))
    store.dispatch(setHamburgerItemsFull(this.props.tutorials))
    store.dispatch(fetchFooterContentSuccess(this.props.footerContent))
    store.dispatch(setImages(this.props.images))
    return (
      <>
        {/* OpenLabsGuideApp is to distinguish from other App classes in other applications */}
        {/* Consider this the body of the application - replaces body tag in CSS during build time */}
        <div className='App OpenLabsGuideApp'>
          <Provider store={store}>
            <GlobalErrorHandler>
              <AppContainer {...this.props} initialRoutes={['/catalog/' + this.props.catalogId]} />
            </GlobalErrorHandler>
          </Provider>
        </div>
      </>
    )
  }
}

export default App
