import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { Link, ClickableTile } from 'carbon-components-react'
import { ArrowRight16, Timer16 } from '@carbon/icons-react'
import store from '../../../reducers/index'
import { setInlineModuleDelimiter, setHasInlineModules, getCourseContentSuccess, setFilename} from '../../../actions/courseActions'
class Tutorials extends React.Component {
  constructor (props) {
    super(props)

    this.state = {}
  }

  componentDidMount () {}

  getLastVisitedCourse (commitId, item) {
    this.props.dispatch(setFilename(item.tutorial_name))
    this.props.dispatch(setInlineModuleDelimiter("H3"));
    this.props.dispatch(setHasInlineModules(true))

    this.props.dispatch(getCourseContentSuccess([
    {
      "index": "01.01",
      "name": item.tutorial_name,
      "content": item.tutorial_content.text
    }
    ]));
    return {
      location: {
        pathname: `/clone/${commitId}/tutorial/${item.tutorial_name}`
      }
    }
  }

  render () {
    const hamburgerItems = this.props.hamburgerItems
    const catalogId = this.props.match.params.catalogId
    const commitId = this.props.commitId
    return (
      <div className={`Tutorials ${this.props.size}`}>
        <div className='Tutorials__info'>
          {this.props.showTitle && <div className='Tutorials__title'>
            Tutorials
          </div>}
          <div className='Tutorials__description'>
              An in-depth collection of tasks and steps for users in order to learn a concept.
          </div>
        </div>
        <div className='Tutorials__course-tiles'>
          {hamburgerItems.map((item, index) => {
            const lastVisitedCourse = this.getLastVisitedCourse(index, item)
            return (
              <ClickableTile className='Tutorials__course-tile' handleClick={() => { this.props.history.push(lastVisitedCourse.location.pathname) }}>
                <div title={item.title} className='Tutorials__course-tile-title'>
                  {item.title}
                </div>
                <div className='Tutorials__course-tile-details'>
                  <div title={item.description} className='description'>{item.description}</div>
                  {item.duration && <span className='duration'><Timer16 className='duration-icon' />{item.duration}</span>}
                  <ArrowRight16 className='icon' />
                </div>
              </ClickableTile>
            )
          })}
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({})

export default withRouter(connect(mapStateToProps)(Tutorials))